.dialog {
  margin: 0 0 20px 0;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.link, .active {
  display: block;
  text-decoration: none;
  font-size: 20px;
  font-weight: bold;
  color: #8a24e8;
  transition: all 0.7s ease 0s;
}
.active {
  color: #ec05be;
  font-size: 28px;
}

.ava{
  flex: 0 0 30px;
  height: 30px;
  border-radius: 50%;
  overflow: hidden;
  margin: 0 10px 0 0;
  & img{
    width: 100%;
  }
}